import React from 'react'

interface Props extends React.HTMLAttributes<HTMLImageElement> {
  src: string
  alt?: string
  size?: number
}

export const Avatar: React.FC<Props> = ({ src, alt = 'avatar', size, ...props }) => {
  return <img src={src} style={{ width: size, height: size, borderRadius: size }} alt={alt} {...props} />
}
